import React, { Component } from "react"
import MetaTags from 'react-meta-tags';
import PropTypes from 'prop-types'
import { Link } from "react-router-dom"
import { connect } from "react-redux"
import Breadcrumbs from "../../components/Common/Breadcrumb"
import { withTranslation } from "react-i18next"
import LeadBySource from "./lead"
import Apaexlinecolumn from "./apaexlinecolumn"
import Revnue from "../AllCharts/apex/revnueLine"
import { Table, Thead, Tbody, Tr, Th, Td } from "react-super-responsive-table"
import WelcomeComp from "./WelcomeComp"

import appointment from "../../assets/images/rsicons/appointment.png"
import activiti from "../../assets/images/rsicons/activities.png"
import call from "../../assets/images/rsicons/call.png"
import notes from "../../assets/images/rsicons/notes.png"
import todo from "../../assets/images/rsicons/to-do.png"

import {
    Row,
    Col,
    CardBody,
    Card,
    CardText,
    Container,
    Nav,
    NavItem,
    NavLink,
    TabContent,
    TabPane,
    FormGroup,
    Label,
    Input,
    Button,
    Select
} from "reactstrap"

class OurResource extends Component {
  constructor(props) {
    super(props)
    this.state = {
      
    }
  }
  render() {
    return (
      <React.Fragment>
        <div className="page-content">
          <MetaTags>
            <title>Resource | Realty Space</title>
          </MetaTags>
          <Container fluid>
             <Breadcrumbs
              title={this.props.t("Resource")}
              breadcrumbItem={this.props.t("Resource")}
            />
           
           <Row>
                          <Col sm="12">
                            <Card>
                              <CardBody>
                                <Row>
                                    <Col sm="3">
                                    <FormGroup className="mb-3">
                                        <Label htmlFor="fname">
                                        {this.props.t("Title")}
                                        </Label>
                                        <input
                                        name=""
                                        
                                        type="text"
                                        
                                        className="form-control"
                                    
                                        id=""
                                        />
                                    </FormGroup>
                                    </Col>

                                    <Col sm="3">
                                    <FormGroup className="mb-3">
                                        <Label htmlFor="fname">
                                        {this.props.t("Type")}
                                        </Label>
                                        <select className="form-control">
                                            <option value="">Select Type</option>
                                            <option value="f">File</option>
                                            <option value="f">Video</option>
                                        </select>
                                    </FormGroup>
                                    </Col>

                                    <Col sm="3">
                                    <FormGroup className="mb-3">
                                        <Label htmlFor="fname">
                                        {this.props.t("File")}
                                        </Label>
                                       <input 
                                       type="file"
                                       className="form-control" />
                                        </FormGroup>
                                    </Col>
                                    <Col sm="3">
                                        <button type="button" className="btn btn-add m28">Add</button>
                                    </Col>
                                </Row>


                            </CardBody>
                            </Card>
                            </Col>
                            </Row>
            
           
           
           
          </Container>
        </div>
      </React.Fragment>
    )
  }
}
OurResource.propTypes = {
  t: PropTypes.any

}
export default connect()(withTranslation()(OurResource))
