import React, { Component } from "react"
import PropTypes from "prop-types"
import { withRouter } from "react-router-dom"
import { connect } from "react-redux"
import MetaTags from 'react-meta-tags';
import { withTranslation } from "react-i18next"
import { Table, Thead, Tbody, Tr, Th, Td } from "react-super-responsive-table"
import "react-super-responsive-table/dist/SuperResponsiveTableStyle.css"

//Lightbox

import {
  Row,
  Col,
  CardBody,
  Card,
  CardText,
  Container,
  Nav,
  NavItem,
  NavLink,
  TabContent,
  TabPane,
  FormGroup,
  Label,
  Input,
  Button
} from "reactstrap"
import classnames from "classnames"
import { AvField, AvForm } from "availity-reactstrap-validation"
//Import Breadcrumb
import Breadcrumbs from "../../components/Common/Breadcrumb"
import InputMask from "react-input-mask"
class CreateAgent extends Component {
  constructor(props) {
    super(props)
    this.state = {
      rows: []
      
    }
  
  }
  handleAddRow = () => {
    const item = {
      name: "",
    };
    this.setState({
      rows: [...this.state.rows, item],
    });
  };

  handleAddRowNested = () => {
    const item1 = {
      name1: "",
    };
    this.setState({
      rows1: [...this.state.rows1, item1],
    });
  };
  handleRemoveRow = (e, idx) => {
    if (idx === "01") {
      document.getElementById("addr" + idx).style.display = "block";
    } else if (typeof idx != "undefined") {
      document.getElementById("addr" + idx).style.display = "none";
    }

  };
  handleRemoveRowNested = (e, idx) => {
    document.getElementById("nested" + idx).style.display = "none";
  };
  
  render() {
    const Date1 = props => (
        <InputMask
          mask="99/99/9999"
          value={props.value}
          className="form-control input-mask"
          onChange={props.onChange}
        >
        </InputMask>
      )
      return (
      <React.Fragment>
        <div className="page-content">

          <MetaTags>
            <title>Create Agent | Realty Space</title>
          </MetaTags>
          <Container fluid>
            {/* Render Breadcrumbs */}
            <Breadcrumbs title={this.props.t("Agent")} breadcrumbItem={this.props.t("Create Agent")} />

            <Row className="">
                          <Col sm="12">
                            <Card>
                              <CardBody>
                            <CardText className="mb-0">
                          <Row className="mb15">
                              <Col sm="4">
                              <FormGroup className="mb-3">
                            <Label htmlFor="fname">
                              {this.props.t("Login Id")}
                            </Label>
                            <input
                            type="text"
                            className="form-control" />
                          </FormGroup>

                              </Col>

                              <Col sm="4">
                              <FormGroup className="mb-3">
                            <Label htmlFor="fname">
                              {this.props.t("Agent Code")}
                            </Label>
                            <input
                            type="text"
                            className="form-control" />
                          </FormGroup>

                              </Col>
                              <Col sm="4">
                              <FormGroup className="mb-3">
                            <Label htmlFor="fname">
                              {this.props.t("Email")}
                            </Label>
                            <input
                            type="text"
                            className="form-control" />
                          </FormGroup>

                              </Col>
                          </Row>


                          <Row className="mb15">
                              <Col sm="4">
                              <FormGroup className="mb-3">
                            <Label htmlFor="fname">
                              {this.props.t("First Name")}
                            </Label>
                            <input
                            type="text"
                            className="form-control" />
                          </FormGroup>

                              </Col>

                              <Col sm="4">
                              <FormGroup className="mb-3">
                            <Label htmlFor="fname">
                              {this.props.t("Last Name")}
                            </Label>
                            <input
                            type="text"
                            className="form-control" />
                          </FormGroup>

                              </Col>
                              <Col sm="4">
                              <FormGroup className="mb-3">
                            <Label htmlFor="fname">
                              {this.props.t("Phone")}
                            </Label>
                            <input
                            type="text"
                            className="form-control" />
                          </FormGroup>

                              </Col>
                          </Row>

                          <Row className="mb15">
                              <Col sm="4">
                              <FormGroup className="mb-3">
                            <Label htmlFor="fname">
                              {this.props.t("Phone 2")}
                            </Label>
                            <input
                            type="text"
                            className="form-control" />
                          </FormGroup>

                              </Col>

                              <Col sm="4">
                              <FormGroup className="mb-3">
                            <Label htmlFor="fname">
                              {this.props.t("Fax")}
                            </Label>
                            <input
                            type="text"
                            className="form-control" />
                          </FormGroup>

                              </Col>
                              <Col sm="4">
                              <FormGroup className="mb-3">
                            <Label htmlFor="fname">
                              {this.props.t("Mobile")}
                            </Label>
                            <input
                            type="text"
                            className="form-control" />
                          </FormGroup>

                              </Col>
                          </Row>

                          <Row className="mb15">
                              <Col sm="4">
                              <FormGroup className="mb-3">
                            <Label htmlFor="fname">
                              {this.props.t("Address 1")}
                            </Label>
                            <input
                            type="text"
                            className="form-control" />
                          </FormGroup>

                              </Col>

                              <Col sm="4">
                              <FormGroup className="mb-3">
                            <Label htmlFor="fname">
                              {this.props.t("Address 2")}
                            </Label>
                            <input
                            type="text"
                            className="form-control" />
                          </FormGroup>

                              </Col>
                              <Col sm="4">
                              <FormGroup className="mb-3">
                            <Label htmlFor="fname">
                              {this.props.t("City")}
                            </Label>
                            <input
                            type="text"
                            className="form-control" />
                          </FormGroup>

                              </Col>
                          </Row>    

                          <Row className="mb15">
                              <Col sm="3">
                              <FormGroup className="mb-3">
                            <Label htmlFor="fname">
                              {this.props.t("Province")}
                            </Label>
                            <input
                            type="text"
                            className="form-control" />
                          </FormGroup>

                              </Col>

                              <Col sm="3">
                              <FormGroup className="mb-3">
                            <Label htmlFor="fname">
                              {this.props.t("Postal Code")}
                            </Label>
                            <input
                            type="text"
                            className="form-control" />
                          </FormGroup>

                              </Col>
                              <Col sm="3">
                              <FormGroup className="mb-3">
                            <Label htmlFor="fname">
                              {this.props.t("Date of Birth")}
                            </Label>
                            <Date1 />
                          </FormGroup>

                              </Col>
                              <Col sm="3">
                              <FormGroup className="mb-3">
                            <Label htmlFor="fname">
                              {this.props.t("Anniversary")}
                            </Label>
                            <Date1 />
                          </FormGroup>

                              </Col>
                          </Row>  

                          <Row>
                            <Col sm="12"><h5>Add importnat dates</h5></Col>
                          </Row>
                          <Row>
                            <Col sm="3">
                            <FormGroup className="mb-3">
                            <Label htmlFor="fname">
                              {this.props.t("Name")}
                            </Label>
                            <input
                            type="text"
                            className="form-control" />
                          </FormGroup>
                            </Col>
                            <Col sm="3">
                            <FormGroup className="mb-3">
                            <Label htmlFor="fname">
                              {this.props.t("Relation")}
                            </Label>
                            <input
                            type="text"
                            className="form-control" />
                          </FormGroup>
                            </Col>
                            <Col sm="2">
                            <FormGroup className="mb-3">
                            <Label htmlFor="fname">
                              {this.props.t("Type")}
                            </Label>
                            <select className="form-control">
                              <option value="">Select</option>
                              <option value="">Birthdate</option>
                              <option value="">Anniversary</option>
                            </select>
                          </FormGroup>
                            </Col>
                            <Col sm="2">
                            <FormGroup className="mb-3">
                            <Label htmlFor="fname">
                              {this.props.t("Date")}
                            </Label>
                            <Date1 />
                          </FormGroup>
                            </Col>
                            <Col sm="2">
                                <button type="button" onClick={this.handleAddRow} className="btn btn-primary m28">Add More</button>
                          
                            </Col>
                          </Row>
                          {this.state.rows.map((item1, idx) => (
                                        <Row className="preData mb15" id={"nested" + idx} key={idx}>
                                          <Col sm="3">
                            <FormGroup className="mb-3">
                            <Label htmlFor="fname">
                              {this.props.t("Name")}
                            </Label>
                            <input
                            type="text"
                            className="form-control" />
                          </FormGroup>
                            </Col>
                            <Col sm="3">
                            <FormGroup className="mb-3">
                            <Label htmlFor="fname">
                              {this.props.t("Relation")}
                            </Label>
                            <input
                            type="text"
                            className="form-control" />
                          </FormGroup>
                            </Col>
                            <Col sm="2">
                            <FormGroup className="mb-3">
                            <Label htmlFor="fname">
                              {this.props.t("Type")}
                            </Label>
                            <select className="form-control">
                              <option value="">Select</option>
                              <option value="">Birthdate</option>
                              <option value="">Anniversary</option>
                            </select>
                          </FormGroup>
                            </Col>
                            <Col sm="2">
                            <FormGroup className="mb-3">
                            <Label htmlFor="fname">
                              {this.props.t("Date")}
                            </Label>
                            <Date1 />
                          </FormGroup>
                            </Col>
                            <Col sm="2">
                                <button type="button"
                                 onClick={e =>
                                  this.handleRemoveRowNested(e, idx)
                                }
                                className="btn btn-primary m28">Remove</button>
                          
                            </Col>
                                        </Row>
                          ))}
                          <div><button type="submit" className="btn btn-primary w-md float-end">{this.props.t("Create")}</button></div>                        
                      </CardText>
                      </CardBody>
                      </Card>
                      </Col>
                      </Row>
                      
          </Container>
        </div>
      </React.Fragment>
    )
  }
}

CreateAgent.propTypes = {
  
  t:PropTypes.any
}





export default connect()(withRouter(withTranslation()(CreateAgent)))